import React, { createContext, useContext, useState, useEffect, useCallback, ReactNode } from 'react';
import { io, Socket } from 'socket.io-client';
import { useAuth } from './AuthContext';
import { useParams } from 'react-router-dom';

// Types
export interface Message {
  _id?: string;
  id?: string;
  content: string;
  userId: string;
  username: string;
  timestamp: Date;
  createdAt: Date;
  roomId: string;
  user?: {
    _id?: string;
    id?: string;
    username: string;
  };
  sender?: {
    _id?: string;
    id?: string;
    username: string;
  };
  type?: 'text' | 'image' | 'video';
  mediaUrl?: string;
}

export interface ChatRoom {
  _id?: string;
  id?: string;
  name: string;
  type?: 'location' | 'event' | 'global';
  subType?: 'regional' | 'city' | 'neighborhood' | 'general';
  participants: number;
  description?: string;
  lastMessage?: any;
  // KORRIGIERT: Flexible location property - untersttzt beide Formate
  location?: {
    type?: string;
    coordinates?: [number, number];
    address?: string;
    city?: string;
    radius?: number;
    latitude?: number;  // Alternative Format
    longitude?: number; // Alternative Format
  };
  distance?: number;
  event?: {
    name: string;
    description: string;
    startDate: string;
    endDate: string;
    maxParticipants: number;
  };
  createdBy?: string;
  isActive?: boolean;
  lastActivity?: string;
}

export interface LocationData {
  latitude: number;
  longitude: number;
}

export interface SocketContextType {
  socket: Socket | null;
  currentRoom: string | null;
  userLocation: LocationData | null;
  currentLocationName: string;
  isLocationLoading: boolean;
  locationAccuracy: number | null;
  joinRoom: (roomId: string) => void;
  sendMessage: (content: string) => void;
  messages: Message[];
  rooms: ChatRoom[];
  chatRooms: ChatRoom[];
  setRooms: (rooms: ChatRoom[]) => void;
  setCurrentRoom: (roomId: string | null) => void;
  setMessages: (messages: Message[]) => void;
  user: any;
  createEventRoom: (eventData: any) => Promise<ChatRoom | null>;
  likeMessage: (messageId: string) => Promise<void>;
}

const SocketContext = createContext<SocketContextType | undefined>(undefined);

export const useSocket = () => {
  const context = useContext(SocketContext);
  if (context === undefined) { // FIXED: Korrekte Syntax mit ffnender Klammer
    throw new Error('useSocket must be used within a SocketProvider');
  }
  return context;
};

interface SocketProviderProps {
  children: ReactNode;
}

export const SocketProvider: React.FC<SocketProviderProps> = ({ children }) => {
  console.log(' DEBUG: SocketProvider initializing...');
  
  const { user } = useAuth();
  const { roomId: urlRoomId } = useParams<{ roomId?: string }>();

  console.log(' DEBUG: SocketProvider - Initial user:', user);
  console.log(' DEBUG: SocketProvider - URL room ID:', urlRoomId);

  // Socket State mit korrekten Types
  const [socket, setSocket] = useState<Socket | null>(null);
  const [currentRoom, setCurrentRoom] = useState<string | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [rooms, setRooms] = useState<ChatRoom[]>([]);
  const [chatRooms, setChatRooms] = useState<ChatRoom[]>([]);

  // Location State
  const [userLocation, setUserLocation] = useState<LocationData | null>(null);
  const [currentLocationName, setCurrentLocationName] = useState<string>('Unknown Location');
  const [isLocationLoading, setIsLocationLoading] = useState<boolean>(false);
  const [locationAccuracy, setLocationAccuracy] = useState<number | null>(null);

  const SOCKET_URL = process.env.REACT_APP_SOCKET_URL || 'http://localhost:1113';
  
  console.log(' DEBUG: SocketProvider - Socket URL:', SOCKET_URL);
  console.log(' DEBUG: SocketProvider - Expected Frontend Port: 1234');
  console.log(' DEBUG: SocketProvider - Expected Backend Port: 1113');

  // Location Name Updater
  const updateLocationName = useCallback(async (latitude: number, longitude: number) => {
    try {
      const response = await fetch(
        `https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${latitude}&longitude=${longitude}&localityLanguage=en`
      );
      const data = await response.json();
      const locationName = data.city || data.locality || data.principalSubdivision || 'Unknown Location';
      setCurrentLocationName(locationName);
      console.log(` Location name updated: ${locationName}`);
    } catch (error) {
      console.error(' Error fetching location name:', error);
      setCurrentLocationName('Unknown Location');
    }
  }, []);

  // Fetch Nearby Rooms
  const fetchNearbyRoomsWithLocation = useCallback(async (latitude: number, longitude: number) => {
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        console.log(' No token available for nearby rooms fetch');
        return;
      }

      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:1113'}/api/chat/rooms/nearby`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ latitude, longitude, radius: 10000 })
      });

      if (response.ok) {
        const nearbyRooms = await response.json();
        console.log(` Found ${nearbyRooms.length} nearby rooms`);
        setChatRooms(nearbyRooms);
      } else {
        console.error(' Failed to fetch nearby rooms:', response.status);
      }
    } catch (error) {
      console.error(' Error fetching nearby rooms:', error);
    }
  }, []);

  // Location Functions
  const getCurrentLocation = useCallback(() => {
    if (!navigator.geolocation) {
      console.error(' Geolocation not supported');
      return;
    }

    if (isLocationLoading) {
      console.log(' Location already loading, skipping...');
      return;
    }

    const handleSuccess = (position: GeolocationPosition) => {
      const { latitude, longitude, accuracy } = position.coords;
      
      console.log(` PROGRESSIVE update: accuracy ${accuracy}m, coords: ${latitude}, ${longitude}`);
      
      const locationData: LocationData = { latitude, longitude };
      setUserLocation(locationData);
      setLocationAccuracy(accuracy);
      setIsLocationLoading(false);
      
      updateLocationName(latitude, longitude);
      fetchNearbyRoomsWithLocation(latitude, longitude);
    };

    const handleError = (error: GeolocationPositionError) => {
      console.error(' Geolocation error:', error.message);
      setIsLocationLoading(false);
    };

    setIsLocationLoading(true);
    navigator.geolocation.getCurrentPosition(handleSuccess, handleError, {
      enableHighAccuracy: true,
      timeout: 10000,
      maximumAge: 300000
    });
  }, [updateLocationName, fetchNearbyRoomsWithLocation, isLocationLoading]);

  // Socket Connection
  const connectSocket = useCallback(() => {
    if (!user?._id && !user?.id) return null;
    
    console.log(` Connecting simple chat for: ${user.username}`);
    
    // Korrekte Socket.IO v4 Syntax
    const newSocket: Socket = io(SOCKET_URL, {
      transports: ['websocket', 'polling'],
      timeout: 20000,
      autoConnect: true
    });

    // Event Listeners mit korrekten Types
    newSocket.on('connect', () => {
      console.log(' Socket connected');
      if (user) { // NULL CHECK hinzugefgt
        newSocket.emit('auth', {
          userId: user._id || user.id,
          username: user.username
        });
      }
    });

    newSocket.on('disconnect', (reason: string) => {
      console.log(' Socket disconnected:', reason);
    });

    newSocket.on('auth-success', () => {
      console.log(' Simple auth successful');
    });

    // Message Listener mit korrekten Types
    newSocket.on('newMessage', (message: Message) => {
      console.log(' New message received:', message);
      setMessages((prev: Message[]) => {
        const isDuplicate = prev.some((m: Message) => 
          m._id === message._id || m.id === message.id
        );
        if (isDuplicate) return prev;
        return [...prev, message];
      });
    });

    // Room Join Listeners
    newSocket.on('joined-room', (data: { roomId: string; messages: Message[]; userCount: number }) => {
      console.log(' Joined room confirmed:', data);
      setCurrentRoom(data.roomId);
      setMessages(data.messages || []);
      
      setChatRooms((prevRooms: ChatRoom[]) => 
        prevRooms.map((room: ChatRoom) => 
          (room._id === data.roomId || room.id === data.roomId)
            ? { ...room, participants: data.userCount }
            : room
        )
      );
    });

    newSocket.on('joinedRoom', (data: any) => {
      console.log(' Joined room confirmed via joinedRoom:', data);
      setCurrentRoom(data.roomId);
      setMessages(data.messages || []);
      
      setChatRooms((prevRooms: ChatRoom[]) => 
        prevRooms.map((room: ChatRoom) => 
          (room._id === data.roomId || room.id === data.roomId)
            ? { ...room, participants: data.userCount }
            : room
        )
      );
    });

    newSocket.on('roomMessages', (messages: Message[]) => {
      console.log(' Room messages received via roomMessages:', messages);
      setMessages(messages || []);
    });

    newSocket.on('room-joined', (data: any) => {
      console.log(' Joined room confirmed via room-joined:', data);
      setCurrentRoom(data.roomId);
      setMessages(data.messages || []);
    });

    newSocket.on('messages-loaded', (messages: Message[]) => {
      console.log(' Messages loaded via messages-loaded:', messages);
      setMessages(messages || []);
    });

    newSocket.on('room-data', (data: any) => {
      console.log(' Room data received via room-data:', data);
      setCurrentRoom(data.roomId);
      setMessages(data.messages || []);
    });

    newSocket.on('userCountUpdate', (data: any) => {
      console.log(' User count updated:', data);
      setChatRooms((prevRooms: ChatRoom[]) => 
        prevRooms.map((room: ChatRoom) => 
          (room._id === data.roomId || room.id === data.roomId)
            ? { ...room, participants: data.userCount }
            : room
        )
      );
    });

    console.log(' ALL socket event listeners registered successfully!');
    
    setSocket(newSocket);
    return newSocket;
  }, [user, SOCKET_URL]);

  // SIMPLE JOIN ROOM - keep room loading intact
  const joinRoom = useCallback((roomId: string) => {
    console.log(' Joining room:', roomId);
    
    if (!socket) {
      const newSocket = connectSocket();
      setTimeout(() => {
        console.log(' EMITTING join-room event after socket creation');
        newSocket?.emit('join-room', roomId);
      }, 1000);
      return;
    }
    
    console.log(' EMITTING join-room event to Simple Chat Server');
    socket.emit('join-room', roomId);
    setCurrentRoom(roomId); // Set immediately for UI
  }, [socket, connectSocket]);

  // ERWEITERTE DEBUGGING SEND MESSAGE
  const sendMessage = useCallback((content: string) => {
    console.log(' DEBUG: sendMessage called with content:', content);
    console.log(' DEBUG: Socket available:', !!socket);
    console.log(' DEBUG: Current room:', currentRoom);
    console.log(' DEBUG: Socket connected:', socket?.connected);
    console.log(' DEBUG: User info:', { id: user?.id, username: user?.username });

    if (!socket || !currentRoom) {
      console.error(' DETAILED ERROR: Missing requirements');
      console.error('   - Socket exists:', !!socket);
      console.error('   - Socket connected:', socket?.connected);
      console.error('   - Current room:', currentRoom);
      console.error('   - User ID:', user?.id || user?._id);
      return;
    }

    const messageData = {
      content: content.trim(),
      chatRoom: currentRoom,
      timestamp: new Date().toISOString(),
      // DEBUGGING: Zustzliche Metadaten
      debug: {
        userId: user?.id || user?._id,
        username: user?.username,
        socketId: socket?.id,
        timestamp: Date.now()
      }
    };

    console.log(' DETAILED Message data:');
    console.log('   Content:', messageData.content);
    console.log('   ChatRoom:', messageData.chatRoom);
    console.log('   Timestamp:', messageData.timestamp);
    console.log('   Debug info:', messageData.debug);
    console.log('   Full object:', JSON.stringify(messageData, null, 2));

    // DEBUGGING: Socket event emission
    console.log(' Emitting sendMessage event...');
    try {
      socket.emit('sendMessage', messageData);
      console.log(' sendMessage event emitted successfully');
    } catch (error) {
      console.error(' Error emitting sendMessage:', error);
    }
  }, [socket, currentRoom, user]);

  // Fge fehlende Dummy-Funktionen hinzu
  const createEventRoom = useCallback(async (eventData: any): Promise<ChatRoom | null> => {
    console.log(' Creating event room:', eventData);
    // TODO: Implementiere Event-Room-Erstellung
    return null;
  }, []);

  // Repariere likeMessage Funktion
  const likeMessage = useCallback(async (messageId: string): Promise<void> => {
    console.log(' Liking message:', messageId);
    // TODO: Implementiere Message-Like-Funktionalitt
  }, []);

  // Context Value
  const value: SocketContextType = {
    socket,
    currentRoom,
    userLocation,
    currentLocationName,
    isLocationLoading,
    locationAccuracy,
    joinRoom,
    sendMessage,
    messages,
    rooms,
    chatRooms,
    setRooms,
    setCurrentRoom,
    setMessages,
    user,
    createEventRoom,
    likeMessage
  };

  // Socket Connection Effect - KORRIGIERT mit besserer Auth-Behandlung
  useEffect(() => {
    if (user?.id && !socket) {
      console.log(' Initiating socket connection...');
      
      // Krzere Wartezeit fr neu registrierte User
      const timer = setTimeout(() => {
        connectSocket();
      }, 500); // Nur 500ms statt 2 Sekunden
      
      return () => clearTimeout(timer);
    }
    
    return () => {
      if (socket) {
        console.log(' Disconnecting socket...');
        socket.disconnect();
      }
    };
  }, [user, socket, connectSocket]);

  // Progressive Location Detection - FIX TIMING ISSUE
  useEffect(() => {
    if (!user?.locationEnabled) return;
    
    console.log(' Starting IMMEDIATE location detection...');
    
    // CRITICAL FIX: Synchrone Token-Prfung + besseres Retry
    const attemptLocationDetection = () => {
      const token = localStorage.getItem('token');
      if (token && !userLocation && !isLocationLoading) {
        console.log(' Token found! Triggering getCurrentLocation immediately');
        getCurrentLocation();
      } else if (!token) {
        console.log(' No token yet, retrying in 1s...');
        setTimeout(attemptLocationDetection, 1000); // Krzere Wartezeit
      } else {
        console.log(' Location already loading or available');
      }
    };

    // SOFORTIGER Start ohne Delay
    const timer = setTimeout(attemptLocationDetection, 100); // Nur 100ms Delay

    return () => clearTimeout(timer);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user?.locationEnabled, getCurrentLocation]);

  // AUTO-JOIN ROOM FROM URL
  useEffect(() => {
    if (urlRoomId && socket && urlRoomId !== currentRoom) {
      console.log(` Auto-joining room from URL: ${urlRoomId}`);
      joinRoom(urlRoomId);
    }
  }, [urlRoomId, socket, currentRoom, joinRoom]);

  // ERWEITERTE DEBUGGING MESSAGE LISTENER
  useEffect(() => {
    if (!socket) {
      console.log(' DEBUG: No socket for message listener');
      return;
    }

    console.log(' DEBUG: Setting up message listeners');

    const handleMessage = (message: any) => {
      console.log(' DETAILED Received newMessage:');
      console.log('   Full message object:', JSON.stringify(message, null, 2));
      console.log('   Message ID:', message._id);
      console.log('   Content:', message.content);
      console.log('   Sender:', message.sender);
      console.log('   ChatRoom:', message.chatRoom);
      console.log('   Timestamp:', message.timestamp);
      
      if (!message.sender) {
        console.error(' DETAILED: Invalid message structure');
        console.error('   Missing sender in message:', message);
        console.error('   Available keys:', Object.keys(message));
        return;
      }

      const formattedMessage: Message = {
        _id: message._id,
        id: message._id,
        content: message.content,
        userId: message.sender._id || message.sender.id,
        username: message.sender.username,
        timestamp: new Date(message.timestamp || message.createdAt),
        createdAt: new Date(message.timestamp || message.createdAt),
        roomId: message.chatRoom?._id || message.chatRoom,
        sender: message.sender,
        type: 'text'
      };

      console.log(' FORMATTED message:');
      console.log('   Formatted object:', JSON.stringify(formattedMessage, null, 2));

      setMessages(prev => {
        console.log(' DEBUG: Current messages count:', prev.length);
        const isDuplicate = prev.some(m => m._id === formattedMessage._id);
        console.log(' DEBUG: Is duplicate?', isDuplicate);
        
        if (isDuplicate) {
          console.log(' Skipping duplicate message:', formattedMessage._id);
          return prev;
        }
        
        const newMessages = [...prev, formattedMessage];
        console.log(' Adding new message. New count:', newMessages.length);
        return newMessages;
      });
    };

    const handleError = (error: any) => {
      console.error(' DETAILED Socket error:');
      console.error('   Error object:', error);
      console.error('   Error message:', error.message);
      console.error('   Error details:', error.details);
      console.error('   Error code:', error.code);
      console.error('   Full error:', JSON.stringify(error, null, 2));
      
      // DEBUGGING: Alert fr Fehler
      if (error.message) {
        alert(`Chat Fehler: ${error.message}`);
      }
    };

    const handleMessageSent = (data: any) => {
      console.log(' DETAILED Message sent confirmation:');
      console.log('   Confirmation data:', JSON.stringify(data, null, 2));
      console.log('   Success:', data.success);
      console.log('   Message ID:', data.messageId);
    };

    const handleConnect = () => {
      console.log(' DETAILED Socket connected:');
      console.log('   Socket ID:', socket.id);
      console.log('   Socket connected:', socket.connected);
      console.log('   Socket transport:', socket.io.engine.transport.name);
    };

    const handleDisconnect = (reason: string) => {
      console.log(' DETAILED Socket disconnected:');
      console.log('   Reason:', reason);
      console.log('   Socket ID:', socket.id);
    };

    // ALLE EVENT LISTENER mit Debugging
    socket.on('newMessage', handleMessage);
    socket.on('error', handleError);
    socket.on('messageSent', handleMessageSent);
    socket.on('connect', handleConnect);
    socket.on('disconnect', handleDisconnect);

    console.log(' All message listeners registered with debugging');

    return () => {
      console.log(' DEBUG: Cleaning up message listeners');
      socket.off('newMessage', handleMessage);
      socket.off('error', handleError);
      socket.off('messageSent', handleMessageSent);
      socket.off('connect', handleConnect);
      socket.off('disconnect', handleDisconnect);
    };
  }, [socket]);

  console.log(' DEBUG: SocketProvider fully initialized');

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
};